/**
 * @module transition
 * @desc Transitions tokens module.
 *
 * @memberof @gympass/yoga-tokens
 */

/**
 * A transition
 * @typedef Transition
 *
 * @type {Object}
 * @property {String} duration - 300ms
 * @property {String} timing - ease
 */
var duration = '300ms';
var timing = 'ease';
/**
 * @type {Border}
 * @default
 */

var transitions = ["all ".concat(duration, " ").concat(timing)];
transitions.duration = duration;
transitions.timing = timing;
transitions["default"] = transitions[0];
export default transitions;